import React from 'react';
// import EasyTransition from 'react-easy-transition'

import Header from './components/Header';
import Widgets from './components/Widgets';
// import { PartLoading } from './components/loading/PartLoading';

var App = React.createClass ({
    displayName: 'App',

    getInitialState: function () {
        return { nowTag: '' }
    },

    componentDidMount: function () {
        $(function () {

            var blogBox = $(document.getElementById("blog")),
                header = $(".header-panel"),
                toTop = $(".to-top");

            var delay = 200;
            var timeout = null;
            $(window).on('scroll', function () {

                //set a timeout to make the animation moves at scroll end
                clearTimeout(timeout);
                var scrollTop = $(this).scrollTop();

                if(scrollTop > 24 && !blogBox.hasClass("fixed")){

                    timeout = setTimeout(function(){
                        blogBox.addClass("read");
                    }, delay);

                }else {

                    timeout = setTimeout(function(){
                        blogBox.removeClass("read");

                        if(scrollTop <= 24) {
                            blogBox.removeClass("fixed");
                        }
                    }, delay);
                }
            });

            // //widget width
            // this.setState({
            //     widgetWidth: blogBox.width() * 0.22
            // });
            // widgetPanel.width(widgetPanel.parent().width() * 0.22);

            //to top button
            toTop.on('click', function () {
                $('html, body').animate({scrollTop:0}, 300);
            });

            // window.addEventListener('resize', this.handleResize);

            //scroll anchor
            if (location.hash) {
                this.shiftWindow();
            }
            window.addEventListener("hashchange", this.shiftWindow);

        }.bind(this));
    },

    handleResize: function () {
        // var blogBox = $(document.getElementById("blog"));


        // widgetPanel.width(widgetPanel.parent().width() * 0.22);
    },

    shiftWindow: function () {
        // console.log(2222) ;
        // scrollBy(0, -82);


        if (location.hash != '' && $(location.hash).length > 0) {
            $("html,body").animate({scrollTop: $(location.hash).offset().top - 82}, 1);
        }


    },

    componentWillUnmount: function() {
        // window.removeEventListener('resize', this.handleResize);

        window.removeEventListener('hashchange', this.shiftWindow);

        $(window).off("scroll");
    },

    setStateFromChild: function (state, action = '') {
        var blogBox = $(document.getElementById("blog"));

        // //widget width
        // this.setState({
        //     widgetWidth: blogBox.width() * 0.22
        // });

        this.setState(state);
    },

    __closeLiveCodeLayer: function (e) {
        e.stopPropagation();

        //button or layer clicked
        if($(e.target).hasClass("close") || $(e.target).hasClass("show")) {
            let iFrame = document.getElementById("live-code-iframe");
            
            $(iFrame).parent().parent().removeClass("show");
            iFrame.parentNode.removeChild(iFrame);
        }
    },

    render: function() {
        const BaseOrigin = this.props.routes[0].BaseOrigin;

        return (
            /*
             <EasyTransition
             path={location.pathname}
             initialStyle={{opacity: 0, marginTop: '-30px'}}
             transition="opacity 0.3s ease-in, margin-top 0.5s"
             finalStyle={{opacity: 1, marginTop: 0}}
             >
             {this.props.children}
             </EasyTransition>
             */

            <div className="blog" id="blog">
                <Header BaseOrigin={ BaseOrigin } />

                <Widgets BaseOrigin={ BaseOrigin }  { ...this.state } />

                <div className="wrapper" id="wrapper">
                    {this.props.children && React.cloneElement(this.props.children, {
                        requestParent: this.setStateFromChild
                    })}

                </div>

                <div className="overlay live-code" onClick={ this.__closeLiveCodeLayer }>
                    <div id="live-code-wrapper">
                        <span className="close" onClick={ this.__closeLiveCodeLayer }>x</span>
                    </div>
                </div>


                <div className="to-top-box">
                    <a className="to-top" href="#">
                        <svg viewBox="0 0 16 16">
                            <path d="M11 1l-5 5h-3l-3 4c0 0 3.178-0.885 5.032-0.47l-5.032 6.47 6.592-5.127c0.919 2.104-0.592 5.127-0.592 5.127l4-3v-3l5-5 1-5-5 1z"></path>
                        </svg>
                    </a>
                </div>
            </div>
        );
    }
});

module.exports = App;





