import React from 'react';
import uuid  from 'node-uuid';

import { showInnerHtml, addClassForAnimate, markedEscape, getInfoByYaml } from '../util/Helper';
import { getConfigData } from '../components/ComponentHelper';

import { PartLoading } from '../components/loading/PartLoading';
import { Comments } from '../components/Comments';
import { LiveCode } from '../components/LiveCode';

var ReactGA = require('react-ga');
ReactGA.initialize('UA-89586643-1');


class Content extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            content: null,
            hasMenu: true,
            articles: [],
            pathname: props.location.pathname,
            articleName: null,

            runCode: null
        };

        this._getContent = this._getContent.bind(this);
        this._runCode = this._runCode.bind(this);
    }

    componentDidMount() {
        try {
            getConfigData(this.props.routes[0].BaseOrigin, (data) => {
                this.saveState({
                    articles: data.articles
                }, '', this._getContent);
            }, this.props);
        }catch (e) {
            console.error(e);

            //route to error-page(or maintenance page -. -)
        }
    }

    componentWillReceiveProps(props) {
        if(props.location.pathname != this.state.pathname) {
            //cancel all request
            window.stop();

            this.saveState(
                {
                    content: null,
                    hasMenu: true,
                    loading: true,
                    pathname: props.location.pathname,
                    runCode: null   //added at 16.11.8
                },
                '',
                this._getContent
            );
        }
    }

    componentWillUnmount() {
        let iFrame = document.getElementById("live-code-iframe");

        if(iFrame) {
            $(iFrame).parent().parent().removeClass("show");
            iFrame.parentNode.removeChild(iFrame);
        }
    }

    saveState(state, action = '', callback) {

        if(action == "force") {
            this.forceUpdate(this._saveCache.bind(this, callback));
        }

        if(!action || action == '') {
            this.setState(state, this._saveCache.bind(this, callback));
        }
    }

    //user action cache
    _saveCache(callback) {
        if(typeof callback == 'function') {
            callback();
        }

        //set widget options
        var widgetOptions = {
            show: 'article',
            articleName: this.state.articleName
        };
        
        if(this.state.articleName == 'about' || this.state.articleName == null) {
            widgetOptions = {
                show: 'tag',
                articleName: null
            }
        }

        this.props.requestParent(widgetOptions);
    }

    _getContent() {
        
        $(function () {

            // console.log(this);
            // return ;

            let { query } = this.props.location,
                articles = this.state.articles;

            const baseOrigin = this.props.routes[0].BaseOrigin,
                articlePath = '/files/';

            var articleName = this.props.params.articleName || null,
                ext = this.props.params.ext || 'md';

            //check if in the list


            //about page
            if(this.props.route.params && this.props.route.params.articleName) {
                articleName = this.props.route.params.articleName;
            } else if (articleName == null || !articles.some((article) => { return article.route == articleName })) {
                this.context.router.push('/');

                return ;
            }

            var url = baseOrigin + articlePath + articleName + '.' + ext;


            var text = '';
            $.ajax({
                url: url,

                cache: false,

                success: function (txt) {

                    var commentIndex = txt.indexOf("---"),
                        head = txt.substring(0, commentIndex);

                    txt = txt.substring(commentIndex + 3);

                    if(head === ''){
                        this.context.router.push('/');
                        return ;
                    }

                    var info = getInfoByYaml(head);

                    var marked = require('marked');

                    var renderer = new marked.Renderer();

                    marked.setOptions({
                        renderer: renderer,
                        gfm: true,
                        tables: true,
                        breaks: false,
                        pedantic: false,
                        sanitize: false,    //allow html tag
                        smartLists: true,
                        smartypants: false,
                        langPrefix: 'language-',
                        headerPrefix: 'toc_'
                    });

                    renderer.code = function (code, lang, escaped) {
                        if (marked.defaults.highlight) {
                            var out = marked.defaults.highlight(code, lang);
                            if (out != null && out !== code) {
                                escaped = true;
                                code = out;
                            }
                        }

                        if (!lang) {
                            return '<pre><code class="common">'
                                + (escaped ? code : markedEscape(code, true))
                                + '\n</code></pre>';
                        }

                        //add a run button
                        let firstLine = code.substring(0, code.indexOf("\n")).trim(),
                            run = false, runType = null;
                        if(firstLine.indexOf("#run") === 0) {
                            run = true;
                            runType = firstLine.split(':')[1];

                            code = code.substring(code.indexOf("\n") + 1);
                        }

                        return run && runType ?
                            (
                            '<pre class="run">'
                            + '<button class="btn btn-primary btn-sm run-code">Run code</button>'
                            + '<textarea class="hide" data-run-type="' + runType.trim()
                            + '">'
                            + code
                            + '</textarea>'
                            + '<code class="'
                            + marked.defaults.langPrefix
                            + markedEscape(lang, true)
                            + '">'
                            + (escaped ? code : markedEscape(code, true))
                            + '\n</code></pre>\n'
                            )
                        :
                            (
                            '<pre><code class="'
                            + marked.defaults.langPrefix
                            + markedEscape(lang, true)
                            + '">'
                            + (escaped ? code : markedEscape(code, true))
                            + '\n</code></pre>\n'
                            );
                    };

                    // renderer.image = (href, title, text) => {
                    //     return title ? `<img class="lazy" data-original="${href}" title="${title}">` : `<img data-original="${href}" >`;
                    // };
                    //
                    // renderer.html = (html) => {
                    //     return html.replace(/<img src=/g, '<img class="lazy" data-original=');
                    // };

                    var count = 0;

                    /**
                     * text => 4. <a href="#toc_29"></a>
                     * raw => 4. [](#toc_29)
                     *
                     * @param text
                     * @param level
                     * @param raw
                     * @returns {string}
                     */
                    renderer.heading = function (text, level, raw) {
                        // if(raw != text) {
                        //     console.log(text, raw);
                        // }
                        return '<h'
                            + level
                            + ' id="'
                            + marked.defaults.headerPrefix + count++
                            + '">'
                            + text
                            + '</h'
                            + level
                            + '>\n';
                    };

                    text = marked(txt);

                    this.saveState({
                        info: info,
                        content: text,
                        loading: false,
                        articleName: articleName
                    }, '', () => {


                        //set document title
                        document.title = this.state.info.title;

                        // Prism.highlightAll();
                        hljs.initHighlighting.called = false;
                        hljs.initHighlighting();


                        //generate the menu
                        var contentBox = $('.content-box'),
                            mdContent = $(".mark-down-content"),
                            h1Menus = mdContent.find("h1"),
                            h2Menus = mdContent.find("h2"),
                            menuBox = $(".mark-down-menu"),
                            menuUl = document.createElement("ul");

                        function generateTree(root, jqueryDom, className) {
                            var li = document.createElement("li"),
                                a = document.createElement("a");

                            if (typeof className == 'undefined' || className != '') {
                                li.className = className;
                            }
                            a.href = "#" + $(jqueryDom).attr("id");
                            a.textContent = $(jqueryDom).text();

                            li.appendChild(a);
                            root.appendChild(li);
                        }

                        h1Menus.each(function () {
                            generateTree(menuUl, this, 'first');
                        });

                        h2Menus.each(function () {
                            generateTree(menuUl, this, 'second');

                            var h3s = $(this).nextUntil("h2", "h3");
                            if (h3s.length != 0) {
                                h3s.each(function () {
                                    generateTree(menuUl, this, 'third');
                                });
                            }
                        });


                        //make h3 as first menu when h1, h2 are missing
                        if(h1Menus.length == 0 && h2Menus.length == 0) {
                            mdContent.find("h3").each(function() {

                                generateTree(menuUl, this, 'second');
                            });
                        }


                        if(menuUl.childNodes.length > 0) {
                            menuBox.children('ul').remove();

                            menuBox.append(menuUl);

                            if (h1Menus.length > 0) {
                                menuBox.find(".first").last().addClass("first-end").width($(window).width() * 0.37);
                            }
                        }else {
                            // menuBox.remove();

                            //maybe here should remove setState, because has called second time
                            this.saveState({ hasMenu: false });
                        }

                        //lazy load
                        // let LazyLoad = require("vanilla-lazyload");
                        // new LazyLoad();
                        // $("img.lazy").lazyload({
                        //     effect : "fadeIn"
                        // });
                        

                        // auto location
                        if (location.hash != '' && $(location.hash).length > 0) {

                            $("html,body").animate({scrollTop: $(location.hash).offset().top - 82}, 1);

                            addClassForAnimate($(location.hash), 'selected', 3100);

                            // $(location.hash).addClass("selected");

                            // console.log();

                            var nextUtilHead = $(location.hash).nextUntil($(location.hash).prop("tagName"));

                            // console.log(nextUtilHead);

                            nextUtilHead.each(function () {
                                addClassForAnimate($(this), 'selected', 3100);
                            });

                            // var next = $(location.hash).next();
                            //
                            // if (next.is("ul")) {
                            //
                            //     addClassForAnimate(next, 'selected', 3100);
                            //
                            //     // next.addClass("selected");
                            // }
                        }

                        //content box link animate
                        contentBox.on('click', 'a', function (e) {
                            var hash = $(this).attr("href");

                            if(location.hash == hash) {
                                e.preventDefault();
                            }

                            var targetObj = $(hash),
                                next = targetObj.next();

                            //todo make <p> & <pre> etc.. selected style
                            if (targetObj.length > 0) {
                                addClassForAnimate(targetObj, 'selected', 3100);


                                var nextUtilHead = targetObj.nextUntil(targetObj.prop("tagName"));

                                nextUtilHead.each(function () {
                                    addClassForAnimate($(this), 'selected', 3100);
                                });

                                // if (next.is("ul")) {
                                //     addClassForAnimate(next, 'selected', 3100);
                                // }
                            }

                        });

                        //run code module
                        let runCodeButton = $("pre.run > button.run-code");

                        runCodeButton.on('click', this._runCode);
                    });
                }.bind(this)
            });
        }.bind(this));
    }

    //run code lively
    _runCode(e) {
        let textArea = e.target.nextElementSibling;

        if(textArea.tagName === 'TEXTAREA') {
            let codeType = textArea.getAttribute("data-run-type");
            let code = textArea.innerText;

            this.saveState({
                runCode: {
                    code: code,
                    codeType: codeType,
                    uuid: uuid()
                }
            }, '', () => {
                ReactGA.event({
                    category: 'Content',
                    action: 'Run Code',
                    label: 'type',
                    value: codeType
                });
            });
        }
    }

    render() {

        if(this.state.loading) {
            return (
                <div className="panel panel-default">
                    <div className="loading-box">
                        <PartLoading />
                    </div>
                </div>
            )

        }

        return (
            <div className="panel panel-default">

                <article className="content-box">

                    <div className="info">
                        <title className="title">
                            <h1>{ this.state.info.title }</h1>
                        </title>
                        <div className="author">
                            <h2>{ this.state.info.author }</h2>
                        </div>

                        <div className="date">
                            <h2>{ this.state.info.date }</h2>
                        </div>

                        <div className="tags">

                        </div>
                    </div>

                    { this.state.hasMenu ?
                        <div className="mark-down-menu">
                            <div className="name">
                                Menu
                            </div>
                        </div>
                        :
                        null
                    }

                    <div className="content">

                        <div className="desc">

                        </div>

                        <div
                            className={ "mark-down-content" + (this.state.info.plain ? ' plain' : "") }
                            dangerouslySetInnerHTML={showInnerHtml(this.state.content)}
                        >

                        </div>

                    </div>

                    <Comments title={ this.state.info.title } />
                    <LiveCode { ...this.state.runCode } />
                </article>


                <div className="clearfix"></div>


            </div>
        );
    }
}

Content.displayName = 'Article';
Content.contextTypes = {
    router: React.PropTypes.object
};


module.exports = Content;



