import React from 'react';
import { Link } from 'react-router';

class Pagination extends React.Component {
    constructor(props) {
        super(props);
    }

    _geyLinkPath(params, maxPage, type) {
        let path = '/';

        for(let x in params) {

            if(!params[x]) {
                continue ;
            }

            if(x == 'p') {
                let page = parseInt(params[x]);
                if(page && page > 1) {
                    if(type == 'next') {

                        if(page === maxPage) {
                            return '';
                        }

                        page = page + 1;
                    }

                    if(type == 'prev') {
                        page = page - 1;
                    }

                    path += x + '/' + page + '/';
                }else {
                    if(type == 'next') {
                        page = 2;

                        path += x + '/' + page + '/';
                    }

                    if(type == 'prev') {
                        return '';
                    }
                }

                continue;
            }

            path += x + '/' + params[x] + '/';
        }

        if(!params.p) {
            if(type == 'next') {
                path += 'p' + '/' + 2 + '/';
            }

            if(type == 'prev') {
                return '';
            }


        }


        return path;
    }

    render() {
        let params = this.props.params || null,
            prevPath = this._geyLinkPath(params, this.props.maxPage, 'prev'),
            nextPath = this._geyLinkPath(params, this.props.maxPage, 'next');

        return (
            <ul className="pager">
                <li>
                    <Link
                        to={ prevPath === '' ?  'javascript:;' : prevPath }
                        className={ prevPath === '' ? 'disabled' : ''}
                        onClick={ (e) => {
                            if(prevPath === '') e.preventDefault();
                        } }
                    >
                        &larr; Previous
                    </Link>

                </li>
                <li>
                    <Link
                        to={ nextPath === '' ?  'javascript:;' : nextPath }
                        className={ nextPath === '' ? 'disabled' : ''}
                        onClick={ (e) => {
                            if(nextPath === '') e.preventDefault();
                        } }
                    >
                        Next &rarr;
                    </Link>
                </li>
            </ul>
        )
    }
}

module.exports = {
    Pagination
};


