let getConfigData = function (BaseOrigin, callback = null, callbackParams) {

    if(!BaseOrigin) {
        throw 'base origin not found';
    }

    if(typeof callback != 'function') {
        throw 'callback is invalid';
    }

    var CONFIG_DATA = sessionStorage.CONFIG_DATA ? JSON.parse(sessionStorage.CONFIG_DATA) : null,
        now = parseInt((new Date).getTime() / 1000);


    if(!CONFIG_DATA || typeof CONFIG_DATA.articles == 'undefined' || (now - CONFIG_DATA.time >= 43200)) {

        const listJsonUrl = BaseOrigin + '/config/config.json';

        function getListData() {
            return new Promise((resolve, reject) => {

                try {
                    $.getJSON(listJsonUrl, (CONFIG) => {
                        let CONFIG_DATA = {
                            articles: CONFIG.articles,
                            tags: CONFIG.tags,
                            time: parseInt((new Date).getTime() / 1000)
                        };

                        sessionStorage.CONFIG_DATA = JSON.stringify(CONFIG_DATA);

                        resolve(CONFIG_DATA);
                    });
                }catch (e) {
                    reject(e);
                }


                // window.fetch(listJsonUrl).then((res) => {
                //     if (res.ok) {
                //         res.json().then((CONFIG) => {
                //
                //             let CONFIG_DATA = {
                //                 articles: CONFIG.articles,
                //                 tags: CONFIG.tags,
                //                 time: parseInt((new Date).getTime() / 1000)
                //             };
                //
                //             sessionStorage.CONFIG_DATA = JSON.stringify(CONFIG_DATA);
                //
                //             resolve(CONFIG_DATA);
                //         });
                //     } else {
                //         // console.log('get list data error')
                //
                //         reject('error');
                //     }
                // });
            });
        }

        getListData().then((CONFIG_DATA) => {
            callback(CONFIG_DATA, callbackParams);
        });
    }else {
        callback(CONFIG_DATA, callbackParams);
    }

};


module.exports = {
    getConfigData
};


