import CONST from '../const';
import Yaml from 'yamljs';

var md5 = require("../../lib/md5/md5");

const CheckTypes = {
    INT: 1,
    STRING: 2,
    ARRAY: 3,

    PHONE: 51,
    EMAIL: 52,
    DATE: 53,

    __SAFE__: 999
};


/**
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 * check type
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 */
//class checkType{
//}

function isNumber(str){
    var n = Number(str);

    return !isNaN(n) && n != '';
}

function isString(str){
    return Object.prototype.toString.call(str) === '[object String]';
}

function isArray(obj){
    return Object.prototype.toString.call(obj) === '[object Array]';
}

function checkMobile(phone) {
    var r = /^1[3,5,7,8]\d{9}$/;
    return r.test(phone);
}

function isEmail(val){
    return !!(val.match(/\S+@\S+\.\S+/) && val.indexOf(' ') == -1 && val.indexOf('..') == -1);
}



function isDate(date){
    var parsedDate = Date.parse(date);

    return isNaN(date) && !isNaN(parsedDate);
}



function check(val, type){
    type = typeof type === 'undefined' ? CheckTypes.STRING : type;

    var pass = false;
    switch (type){
        case CheckTypes.INT:
            pass = isNumber(val);
            break;

        case CheckTypes.STRING:
            pass = isString(val);
            break;

        case CheckTypes.ARRAY:
            pass = isArray(val);
            break;

        case CheckTypes.PHONE:
            pass = checkMobile(val);
            break;

        case CheckTypes.DATE:
            pass = isDate(val);
            break;

        case CheckTypes.EMAIL:
            pass = isEmail(val);
            break;


        case CheckTypes.__SAFE__:
            pass = true;
            break;

        default:
            pass = false;
    }

    return pass;
}


function validate(validateKeys, params) {
    for (var key in validateKeys){
        if(typeof params[key] == 'undefined' || !check(params[key], validateKeys[key])){
            throw key;
        }
    }
}



/**
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
 *
 * other func
 *
 * - - - - - - - - - - - - - - - - - - - - - - - - - - - - - -
*/

function checkBroswer(){
    var browser={
        versions:function(){
            var u = navigator.userAgent;
            return {         //
                trident: u.indexOf('Trident') > -1, //IE
                presto: u.indexOf('Presto') > -1, //opera
                webKit: u.indexOf('AppleWebKit') > -1, //
                gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
                mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
                ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
                android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
                iPhone: u.indexOf('iPhone') > -1 , //iPhoneQQHD
                iPad: u.indexOf('iPad') > -1, //iPad
                webApp: u.indexOf('Safari') == -1 //web
            };
        }(),
        language:(navigator.browserLanguage || navigator.language).toLowerCase()
    };

    return browser;
}

function isWeiXin() {
    var ua = window.navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == 'micromessenger';
}



function generateKey(time){
    return md5(time + '-' + CONST.TOKEN_KEY + 'qaz');
}


function beforeToday(date) {
    var date = new Date(date), today = new Date(),
        year = parseInt(date[0]), month = parseInt(date[1]) - 1, day = parseInt(date[2]),
        todayYear = today.getFullYear(), todayMonth = today.getMonth(), todayDay = today.getDate();

    if(year < todayYear) {
        return true;
    }

    if(month == todayMonth){
        if(month < todayMonth) {
            return true;
        }

        if(month == todayMonth) {
            return day < todayDay;
        }
    }

    return false;
}

function isToday(date) {
    var date = new Date(date), today = new Date();

    return date.getFullYear() == today.getFullYear() && date.getMonth() == today.getMonth() && date.getDate() == today.getDate();
}

function showInnerHtml(html){
    return {__html: html};
}

//like add 'selected' class, use 'time' for removing the class
function addClassForAnimate(jqueryDom, className, time) {

    if(!jqueryDom.hasClass(className)) {

        jqueryDom.addClass(className);

        if(typeof time != 'undefined' && time != 0) {
            setTimeout(function () {

                jqueryDom.removeClass(className);
            }, time);
        }
    }

}


// marked helper funcitons
function markedEscape(html, encode) {
    return html
        .replace(!encode ? /&(?!#?\w+;)/g : /&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/'/g, '&#39;');
}


//read yaml info
let getInfoByYaml = function (string) {

    var originInfo = {
        title: '~',
        author: 'azlar',
        date: '',
        tags: []
    };
    var info = Yaml.parse(string);

    // if(info && typeof info.date != 'undefined') {
    // }

    return Object.assign(originInfo, info);
};


