function isElementInViewport (el) {
    //special bonus for those using jQuery
    if (typeof jQuery === "function" && el instanceof jQuery) {
        el = el[0];
    }

    var rect = el.getBoundingClientRect();

    return (
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) && /*or $(window).height() */
        rect.right <= (window.innerWidth || document.documentElement.clientWidth) /*or $(window).width() */
    );
}



//substr ch & en
function getCharLength(str) {
    var c = 0.0;
    var unicode = 0;
    if (str == null || str == "") {
        return 0;
    }
    var len = str.length;
    for(var i = 0; i < len; i++) {
        unicode = str.charCodeAt(i);
        if (unicode < 127) { //
            c += 1;
        } else {  //chinese
            c += 2;
        }
    }
    return c;
}

//
function subStrCh(str, start, end) {
    var  c = 0, strUnicode = '', newStr = '';
    var len = str.length;
    var strLen = getCharLength(str);
    if (start < 0) {
        start = strLen + start;
    }
    if (end < 1) {
        end = strLen + end;// - ((str.charCodeAt(len-1) < 127) ? 1 : 2);
    }

    for(var i = 0; i < len; i++) {
        if (c >= start) {
            break;
        }
        strUnicode = str.charCodeAt(i);
        if (strUnicode < 127) {
            c += 1;
        } else {
            c += 2;
        }
    }

    for(i; i < len; i++) {
        strUnicode = str.charCodeAt(i);
        if (strUnicode < 127) {
            c += 1;
        } else {
            c += 2;
        }
        newStr += str.charAt(i);
        if (c >= end) {
            break;
        }
    }
    return newStr;
}




module.exports = {
    isNumber: isNumber,
    isString: isString,
    isArray: isArray,
    checkMobile: checkMobile,
    isEmail: isEmail,
    isDate: isDate,

    checkBroswer: checkBroswer,
    isWeiXin: isWeiXin,
    generateKey: generateKey,
    validate: validate,
    beforeToday: beforeToday,
    isToday: isToday,
    showInnerHtml: showInnerHtml,
    addClassForAnimate: addClassForAnimate,

    markedEscape: markedEscape,

    getInfoByYaml: getInfoByYaml,


    getCharLength,
    subStrCh,

    //internet
    isElementInViewport,
    
    //CONST
    CheckTypes: CheckTypes
    
};


