import React from 'react';


var PartLoading = React.createClass({

    componentWillMount: function () {
    },

    render: function () {
        return (
            <div className="part-loading-box">
                <div className="spin">
                    <span className="first dot" />
                    <span className="second dot" />
                    <span className="third dot" />
                </div>
            </div>
        );
    }
});


module.exports = {PartLoading: PartLoading};


