
// import { browserHistory } from 'react-router';
import React from 'react';
import ReactDom from 'react-dom';
import { Router, Route, IndexRoute , hashHistory, browserHistory, Redirect } from 'react-router';

// var ReactDom = require('react-dom'),
//     ReactRouter = require('react-router'),
//     Router = ReactRouter.Router,
//     IndexRoute = ReactRouter.IndexRoute,
//     Route = ReactRouter.Route,
var history = browserHistory,
    $ = require('jquery');

window.jQuery = $;
window.$ = $;

//ga
var ReactGA = require('react-ga');
ReactGA.initialize('UA-89586643-1');

const EnvConfigJson = require('../source/config/env.json');

var BaseOrigin = EnvConfigJson.PROD_URL;

var App = require("./AppComponent"),
    List = require("./pages/ListComponent"),
    Content = require("./pages/ContentComponent"),
    Test = require("./TestComponent");

require("../lib/bootstrap-custom/bootstrap.css");
require("../css/sass/mark-down-custom.scss");
require("../css/sass/content.scss");
// require("../css/test.css");

require("../css/loading.css");
require("../css/main.css");
// require("../css/modules.css");


// require("../lib/prism/prism.js");
// require("../lib/prism/prism.css");

// require("../../lib/highlight/highlight.pack");
// require("../lib/highlight/styles/darcula.css");

// require('jquery-lazyload');


//<IndexRoute component={List} />

function logPageView() {
    //scroll to top
    window.scrollTo(0, 0);

    //ga log
    ReactGA.set({ page: window.location.pathname });
    ReactGA.pageview(window.location.pathname);
}

ReactDom.render(
    <Router history={history} onUpdate={ logPageView } >
        <Route path="/" component={App} BaseOrigin={BaseOrigin}>

            <Route path='article/:articleName(/:ext)/' component={Content} />

            <Route path='about/' component={ Content } params={{ articleName: 'about' }} />

            <Route path='(tag/:tag/)(p/:p/)' component={ List } />
            
            <Route path="t" component={ Test } />

            <Redirect from="*" to="*/" />

            <Route path="*" component={ Test } />

            <Route path="404" component={ Test } />

            <IndexRoute component={ List } />


        </Route>
    </Router>
    ,
    document.getElementById('qwe')
);


