import React from 'react';
import { Link } from 'react-router';

import { Tags } from './Tags';

import { getCharLength, subStrCh } from '../util/Helper';

class Articles extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            ORIGIN_PATH: props.origin || window.location.origin,
            articles: props.articles || [],
            type: props.type || 'widget',

            title: props.title || false,
            nowTag: props.nowTag,
            widgetPanelWidth: 0
        };

        this.mouseOver = false;

        this.wheelHandler = this.wheelHandler.bind(this);

        this._getArticles = this._getArticles.bind(this);
        this.handleResize = this.handleResize.bind(this);
    }

    componentDidMount() {
        //bind a scroll event
        // $(".article-box.widget").on( 'mousewheel DOMMouseScroll', function ( e ) {
        //     var event = e.originalEvent,
        //         d = event.wheelDelta || -event.detail;
        //
        //     this.scrollTop += ( d < 0 ? 1 : (Math.abs(d) == 0 ? 0 : -1) ) * 15;
        //     e.preventDefault();
        // });


        if(this.state.type == 'widget') {
            var widgetPanel = document.getElementById("widget-panel");
            if(null !== widgetPanel) {

                this.setState({
                    widgetPanelWidth: parseInt(window.getComputedStyle(widgetPanel).width)
                }, () => {
                    // console.log(this.state)
                });
            }

            window.addEventListener('resize', this.handleResize);
        }
    }

    componentWillUnmount() {
        $(".article-box.widget").off( 'mousewheel DOMMouseScroll' );

        window.removeEventListener('resize', this.handleResize);
    }

    componentWillReceiveProps(props) {
        
        this.state = {
            ORIGIN_PATH: props.origin || window.location.origin,
            articles: props.articles || [],
            type: props.type || 'widget',

            title: props.title || false,
            nowTag: props.nowTag
        };

        if(this.state.type == 'widget') {
            var widgetPanel = document.getElementById("widget-panel");
            if(null !== widgetPanel) {

                this.setState({
                    widgetPanelWidth: parseInt(window.getComputedStyle(widgetPanel).width)
                }, () => {
                    // console.log(this.state)
                });

            }

            window.addEventListener('resize', this.handleResize);
        }
    }

    handleResize() {

        //wait for resize end
        clearTimeout(window.resizedFinished);
        window.resizedFinished = setTimeout(() => {
            var widgetPanel = document.getElementById("widget-panel");
            if(null !== widgetPanel) {

                this.setState({
                    widgetPanelWidth: document.body.offsetWidth * 0.22 * 0.9
                    // widgetPanelWidth: parseInt(window.getComputedStyle(widgetPanel).width)
                }, () => {
                    // console.log(this.state)
                });
            }

            // alert('Resized finished.');
        }, 250);

    }

    _getArticles(articles) {
        switch (this.state.type) {
            case 'widget':

                var maxFontCount = 15;

                //'padding: 7px;' & '20xx-xx-xx' 10 character width + ...
                var computedArticleBoxWidth = parseInt(this.state.widgetPanelWidth) - 14 - 18 * 7;

                maxFontCount = parseInt(computedArticleBoxWidth / 7);
                
                return(
                    articles.map((article, index) => {

                        var charLength = getCharLength(article.title);
                        return (
                            <Link
                                className="article-block"
                                to={{ pathname: '/article/' + article.route }}
                                key={ 'list-article-' + index + '-' }
                                data-title={ charLength > maxFontCount ? article.title : null }
                            >

                                { charLength > maxFontCount ? subStrCh(article.title, 0, maxFontCount) + '...' : article.title }

                                <span className="time">
                                    { article.createTime.substring(0, article.createTime.indexOf(" ")) }
                                </span>
                            </Link>
                        );
                    })
                );

            case 'list':

                return(
                    articles.map((article, index) => {
                        return (
                            <div
                                className={ "article panel"}
                                key={ 'list-article-' + index + '-panel'}
                            >

                                <h2 className="name">
                                    <Link to={{ pathname: '/article/' + article.route }}>
                                        { article.title }
                                    </Link>
                                </h2>

                                <Tags tags={ article.tags } type="string" origin={ this.state.ORIGIN_PATH } nowTag={ this.state.nowTag } />

                                <div className="author">
                                    { article.author }
                                </div>

                                <div className="create-time">
                                    { article.createTime }
                                </div>

                                <div className="preview">
                                    { article.preview }
                                </div>
                            </div>
                        );
                    })
                );



            default:
                return null;
        }

    }

    wheelHandler(e) {
        if(this.mouseOver && this.state.type === 'block') {
            e.deltaY = e.deltaY >= 50 ? 50 : e.deltaY;
            e.deltaY = e.deltaY <= -50 ? -50 : e.deltaY;

            e.currentTarget.scrollTop += e.deltaY;
            e.preventDefault();

            //end
            if (
                e.currentTarget.scrollTop === (e.currentTarget.scrollHeight - parseInt(window.getComputedStyle(e.currentTarget).height))
                && e.deltaY > 0
            ) {
                e.stopPropagation();
                e.preventDefault();
            }

            //top
            if (e.currentTarget.scrollTop === 0 && e.deltaY < 0) {
                e.stopPropagation();
                e.preventDefault();
            }
        }

    }

    render() {
        return (
            <div
                className={ "article-box " + this.state.type}
                onWheel={ this.wheelHandler }
                onScroll={ () => this.mouseOver = true }
                onMouseEnter={ () => this.mouseOver = true }
                onMouseLeave={ () => this.mouseOver = false }
            >
                { this._getArticles(this.state.articles) }
            </div>
        )
    }
}

module.exports = {
    Articles
};


