import React from 'react';
import { Link } from 'react-router';

import { getConfigData } from '../components/ComponentHelper';

import { PartLoading } from '../components/loading/PartLoading';

import { Tags } from '../components/Tags';
import { Articles } from '../components/Articles';
import { Pagination } from '../components/Pagination';


class List extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            articles: [],
            loading: true,
            nowTag: props.params.tag || 'all',

            paging: false,
            maxPage: 0
        };
        
        this._preRender = this._preRender.bind(this);
    }

    componentDidMount() {
        $(function () {
            try {
                getConfigData(this.props.routes[0].BaseOrigin, this._preRender, this.props);
            }catch (e) {
                console.error(e);

                //route to error-page(or maintenance page -. -)
            }

        }.bind(this));
    }

    componentWillReceiveProps(props) {
        if(props.location.pathname != this.props.location.pathname) {

            this.saveState(
                {
                    loading: true
                    // nowTag: null
                },
                '',
                () => {
                    setTimeout(() => {
                        try {
                            getConfigData(this.props.routes[0].BaseOrigin, this._preRender, props);
                        }catch (e) {
                            console.error(e);

                            //route to error-page(or maintenance page -. -)
                        }
                    }, 10);
                }
            );
        }
    }

    componentWillUnmount() {
         $(".tag-block").removeClass("selected");
        // console.log('here');
        
    }

    _preRender(data, props) {
        let articles = data.articles,
            pageSize = 10,
            paging = false,
            maxPage = 0;

        if(props.routeParams.tag) {
            // this.articles.data.articles.splice()

            var tag = props.routeParams.tag.replace(/\-/g," ") || null,
                articleMap = data.tags[tag] || [];

            // console.log(data.articles);
            // console.log(data.tags);


            articles = articleMap.length > 0 ?
                articles.filter((article, index) => {
                    return articleMap.includes(index);
                })
                :
                articles;
        }

        maxPage = Math.ceil(articles.length / pageSize);


        let page = parseInt(props.routeParams.p) || 1;
        if(
            articles.length > pageSize
            && page > 0
            && page <= maxPage
        ) {
            paging = true;
        }

        if(page !== null) {
            let start = (page - 1) * pageSize;

            articles = articles.slice(start, start + pageSize);
        }


        this.saveState(
            {
                //props.params.tag is from react-router
                nowTag: props.params.tag || 'all',
                articles: articles,
                loading: false,

                paging: paging,
                maxPage: maxPage
            }
        );
    }


    saveState(state, action = '', callback) {

        if(action == "force") {
            this.forceUpdate(this._saveCache.bind(this, callback));
        }

        if(!action || action == '') {
            this.setState(state, this._saveCache.bind(this, callback));
        }
    }

    //user action cache
    _saveCache(callback) {
        if(typeof callback == 'function') {
            callback();
        }

        //set now tag
        //$(".tag-box > a").removeClass("selected");
        //$(".tag-" + this.state.nowTag).addClass("selected");


        //set document title
        document.title = (this.state.nowTag == 'all' ? '' : (this.state.nowTag + ' ')) + "azlar's library - github~~";

        //trigger widget <Tags />
        this.props.requestParent({ nowTag: this.state.nowTag, show: 'tag' });
    }
    
    render() {
        if(this.state.loading) {
            return (
                <div className="panel panel-default">
                    <div className="loading-box">
                        <PartLoading />
                    </div>
                </div>
            )
        }

        return (
            <div className="list-panel">
                <Articles articles={ this.state.articles } nowTag={ this.state.nowTag } type="list" />

                { this.state.paging ?
                    <Pagination params={ this.props.params } maxPage={ this.state.maxPage }/>
                    :
                    null
                }

            </div>
        );
    }
}

List.contextTypes = {
    router: React.PropTypes.object
};

module.exports = List;


