import React from 'react';

class LiveCode extends React.Component {
    constructor(props) {
        super(props);

        //
        // let html = this.__getFrameCode(props.code, props.codeType);
        //
        // this.state = {
        //     html: html
        // }
    }

    componentDidMount() {
        // var iFrame = document.createElement('iframe');
        //
        // iFrame.id = 'test';
        //
        // document.getElementById("live-code-wrapper").appendChild(iframe);
        // iFrame.contentWindow.document.open();
        // iFrame.contentWindow.document.write(this.state.html);
        // iFrame.contentWindow.document.close();
    }

    componentWillReceiveProps(props) {
        if(props.code && props.codeType) {
            if(this.props.uuid !== props.uuid) {
                let html = this.__getFrameCode(props.code, props.codeType);

                if(html == null) {
                    return ;
                }

                var iFrame = document.getElementById('live-code-iframe') || document.createElement('iframe'),
                    wrapper = document.getElementById("live-code-wrapper");

                iFrame.id = 'live-code-iframe';

                if(!$(wrapper).parent().hasClass("show")){
                    $(wrapper).parent().addClass("show");
                }

                wrapper.appendChild(iFrame);
                iFrame.contentWindow.document.open();
                iFrame.contentWindow.document.write(html);
                iFrame.contentWindow.document.close();
            }
        }else {
            //remove the layer
            let iFrame = document.getElementById("live-code-iframe");

            if(iFrame) {
                $(iFrame).parent().parent().removeClass("show");
                iFrame.parentNode.removeChild(iFrame);
            }
        }


    }

    __getFrameCode(code, codeType) {

        if(code && codeType) {

            let header = '<!DOCTYPE HTML><html><head><style>html{width: 100%;height: 100%;} body {width: 100%;height: 100%;margin:0;padding:0;text-align:center}</style></head><body>', footer = '</script></body></html>';

            switch (codeType) {
                case 'canvas':
                    header += "<canvas id='canvas' width='578' height='200'></canvas><script>";
            }

            return header + code + footer;
        }

        return null;
    }

    render() {
        return (
            <noscript />
        )
    }
}

module.exports = {
    LiveCode
};


