import { Link } from 'react-router';

var React = require('react');


var imgUrl = '/images/ace.gif',
    content = "Azlar's Library";

var Header = React.createClass({
    displayName: 'Header',

    _getNewState: function () {
        //todo get-name & avatar & menus
    },

    _bindEvents: function () {
        $(function () {


        }.bind(this));
    },

    getInitialState: function() {
        return {
            menus: [
                {name: '', url: '/'},
                {name: 'about', url : '/about/'}
            ]
        };
    },

    componentDidMount: function() {
        setTimeout(function() {
            //this._getNewState();
            this._bindEvents();
        }.bind(this), 50);
    },

    render: function() {
        return (
            <div className="panel panel-default header-panel clearfix">
                <div className="logo">
                <div
                    className="avatar"
                    style={ {backgroundImage: 'url(' + this.props.BaseOrigin + imgUrl + ')'} }
                    onClick={ (e) => {
                        let blogContainer = $(document.getElementById("blog"));
                        // if(blogContainer)
                        let scrollTop = window.pageYOffset || document.documentElement.scrollTop;

                        if(scrollTop > 24) {
                            blogContainer.toggleClass("read");
                            blogContainer.toggleClass("fixed");
                        }
                    }}
                ></div>

                </div>

                <div className="blog-name">
                        <span>
                            { content }
                        </span>
                </div>

                <div className="search-box">
                </div>

                <div className="menus">
                        {this.state.menus.map((menu, index) =>
                            <Link to={menu.url} key={'menu-' + index}>{menu.name}</Link>
                        )}
                </div>
            </div>
        );
    }
});

//module.exports = Header;

export default Header;


