import React from 'react';
import { Link } from 'react-router';

import { isElementInViewport, getCharLength, subStrCh } from '../util/Helper';

class Tags extends React.Component {
    constructor(props) {
        super(props);

        
        this.state = {
            ORIGIN_PATH: props.origin || window.location.origin,
            tags: props.tags || [],
            type: props.type || 'block',

            title: props.title || false,

            nowTag: props.nowTag,


            widgetPanelWidth: 0
        };

        this.mouseOver = false;

        this.wheelHandler = this.wheelHandler.bind(this);

        this._getTags = this._getTags.bind(this);
        this.handleResize = this.handleResize.bind(this);
    }

    componentDidMount() {
        //bind a scroll event
        // $(".tag-box.block").on( 'mousewheel DOMMouseScroll', function ( e ) {
        //     var event = e.originalEvent,
        //         d = event.wheelDelta || -event.detail;
        //
        //     this.scrollTop += ( d < 0 ? 1 : (Math.abs(d) == 0 ? 0 : -1) ) * 15;
        //     e.preventDefault();
        // });

        //block only
        if(this.state.type == 'block') {
            var widgetPanel = document.getElementById("widget-panel");

            // console.log(document.body.offsetWidth);
            if(null !== widgetPanel) {
                this.setState({
                    widgetPanelWidth: document.body.offsetWidth * 0.22 * 0.9
                    // widgetPanelWidth: parseInt(window.getComputedStyle(widgetPanel).width)
                }, () => {
                    // console.log(this.state)
                });
            }

            window.addEventListener('resize', this.handleResize);
        }

    }

    componentWillReceiveProps(props) {
        
        this.setState({
            ORIGIN_PATH: props.origin || window.location.origin,
            tags: props.tags || [],
            type: props.type || 'block',

            title: props.title || false,
            nowTag: props.nowTag
        }, () => {

            var selectedTag = $(".tag-block.selected");

            if(selectedTag.length > 0 && !isElementInViewport(selectedTag)) {
                selectedTag[0].scrollIntoView({ behavior: "smooth" });
            }
        });
    }

    componentWillUnmount() {
        $(".tag-box.block").off( 'mousewheel DOMMouseScroll' );

        window.removeEventListener('resize', this.handleResize);
    }

    handleResize() {

        //wait for resize end
        clearTimeout(window.resizedFinished);
        window.resizedFinished = setTimeout(() => {
            var widgetPanel = document.getElementById("widget-panel");
            if(null !== widgetPanel) {

                this.setState({
                    widgetPanelWidth: parseInt(window.getComputedStyle(widgetPanel).width)
                }, () => {
                    console.log(this.state)
                });
            }

            // alert('Resized finished.');
        }, 250);

    }

    _getTags(tags) {

        switch (this.state.type) {
            case 'block':
                var sortedKeys = Object.keys(tags).sort(function(a,b){return tags[b].length - tags[a].length}),
                    maxFontCount = 15;

                //'padding: 7px;' & count width
                var computedTagBoxWidth = parseInt(this.state.widgetPanelWidth) - 14 - 4 * 7 - 8 * 7;
                
                maxFontCount = parseInt(computedTagBoxWidth / 7);

                return(
                    sortedKeys.map((tagName, index) => {
                        let count = tags[tagName].length,
                            showName= tagName;
                        tagName = tagName.replace(/\s+/g,"-");

                        var charLength = getCharLength(showName);
                        return (
                            <Link
                                className={ "tag-block " + 'tag-' + tagName + (this.state.nowTag == tagName ? ' selected' : '')}
                                key={ 'tag-key-block-' + index + '-' + tagName }
                                to={ '/tag/' + tagName + '/' }
                                data-title={ charLength > maxFontCount ? showName : null }
                            >
                                { charLength > maxFontCount ? subStrCh(showName, 0, maxFontCount) + '...' : showName }

                                <span className="count">
                                    { count }
                                </span>
                            </Link>
                        );
                    })
                );

            case 'string':
                return (
                    tags.map((tag, index) => {
                        let showName = tag;
                        tag = tag.replace(/\s+/g,"-");
                        return (
                            <Link
                                className={ "tag-string " + 'tag-' + tag + (this.state.nowTag == tag ? ' selected' : '')}
                                key={ 'tag-key-string-' + index + '-' + tag }
                                to={ '/tag/' + tag + '/' }
                            >
                                { showName }
                            </Link>
                        );
                    })
                );

            default:
                return null;
        }

    }

    wheelHandler(e) {
        if(this.mouseOver && this.state.type === 'block') {
            e.deltaY = e.deltaY >= 50 ? 50 : e.deltaY;
            e.deltaY = e.deltaY <= -50 ? -50 : e.deltaY;

            e.currentTarget.scrollTop += e.deltaY;
            e.preventDefault();

            //end
            if (
                e.currentTarget.scrollTop === (e.currentTarget.scrollHeight - parseInt(window.getComputedStyle(e.currentTarget).height))
                && e.deltaY > 0
            ) {
                e.stopPropagation();
                e.preventDefault();
            }

            //top
            if (e.currentTarget.scrollTop === 0 && e.deltaY < 0) {
                e.stopPropagation();
                e.preventDefault();
            }
        }

    }

    render() {
        return (
            <div
                className={ "tag-box " + this.state.type }
                onWheel={ this.wheelHandler }
                onScroll={ () => this.mouseOver = true }
                onMouseEnter={ () => this.mouseOver = true }
                onMouseLeave={ () => this.mouseOver = false }
            >
                { this.state.type == 'block' ?
                    <Link
                        id="all-tags" 
                        className={ "tag-block " }
                        to={ '/' }
                    >
                        All

                            <span className="count">
                                { this.props.allCount }
                            </span>
                    </Link>
                    :
                    null
                }

                { this._getTags(this.state.tags) }
            </div>
        )
    }
}

module.exports = {
    Tags
};



