import React from 'react';

import { getConfigData } from './ComponentHelper';
import { Tags } from './Tags';
import { Articles } from './Articles';

class Widgets extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            tags: [],
            articles: [],
            allCount: 0,
            nowTag: props.nowTag,
            show: props.show || 'tag',
            articleName: props.articleName || null
        };

        this._preRender = this._preRender.bind(this);
    }

    componentDidMount() {
        try {
            getConfigData(this.props.BaseOrigin, this._preRender);
        }catch (e) {
            console.error(e);

            //route to error-page(or maintenance page -. -)
        }

        //bind a scroll event
        //$(document.getElementById("widget-panel")).on( 'mousewheel DOMMouseScroll', function ( e ) {
        //    var event = e.originalEvent,
        //        d = event.wheelDelta || -event.detail;
        //
        //    this.scrollTop += ( d < 0 ? 1 : (Math.abs(d) == 0 ? 0 : -1) ) * 30;
        //    e.preventDefault();
        //});
    }

    componentWillReceiveProps(props) {
        
        if(props.nowTag != this.state.nowTag
            || props.show != this.state.show
            || props.articleName != this.state.articleName
        ) {
            // console.log('true' + props.nowTag);

            try {
                getConfigData(this.props.BaseOrigin, (data) => {
                    // console.log(props);
                    this.setState({
                        articles: data.articles,
                        tags: data.tags,
                        allCount: data.articles.length,
                        nowTag: props.nowTag,
                        show: props.show || 'tag',
                        articleName: props.articleName || null
                    });
                });
            }catch (e) {
                console.error(e);

                //route to error-page(or maintenance page -. -)
            }

            // this.setState({
            //     nowTag: props.nowTag,
            //     show: props.show || 'tag',
            //     articleName: props.articleName || null
            // });
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        return nextState.nowTag != this.state.nowTag
            || nextState.nowTag == ''
            || nextState.show != this.state.show
            || nextState.articleName != this.state.articleName;
    }
    _preRender(data) {
        this.setState({
            articles: data.articles,
            tags: data.tags,
            allCount: data.articles.length
        });
    }

    render() {

        var articles = [];
        if(this.state.show == 'article' && this.state.articleName != null) {
            let nowArticle = this.state.articles.find((article, index) => {
                return article.route == this.state.articleName;
            });

            articles = nowArticle.related.length > 0 ?
                this.state.articles.filter((article, index) => {
                    return nowArticle.related.includes(index) && article.route != this.state.articleName;
                })
                :
                articles;
        }

        
        return (
            <div
                className="panel panel-default widget-panel"
                id="widget-panel"
            >

                <title className="head">
                    { this.state.show == 'tag' ? 'Tags' : 'Related Posts' }
                </title>


                {
                    this.state.show == 'tag' ?
                        <Tags
                            tags={ this.state.tags }
                            allCount={ this.state.allCount }
                            nowTag={ this.state.nowTag }
                        />
                        :
                        articles.length > 0 ?
                            <Articles
                                articles={ articles }
                                nowTag={ this.state.nowTag }
                                type="widget"
                            />
                            :
                            <span className="msg">
                                Nothing Found
                            </span>
                }
            </div>
        )
    }
}



module.exports = Widgets;




